import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import createToken from 'src/services/create-token';
import retrieveUserData from 'src/services/retrieve-user-data';
import { API_STATUS, ERROR_MESSAGES } from 'src/static/api';
import { APPROVER_ID } from 'src/static/mobile-key-status';
import { formatDDMMMYYYY } from 'src/utils/date';
import { prepareBody, updateStatusForUser } from 'src/utils/issuance-summary';
import { getId, getNormalizedUserData, getUserData } from 'src/utils/mobile-key-status';
import userList from 'src/__mocks__/user-list';

const initialState = {
    isAuthenticated: false,
    token: '',
    name: 'Admin',
    emailId: APPROVER_ID
};


export const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setToken: (state, action) => {
            state.token = action.payload
        },
        updateAuthentication: (state, action) => {
            state.isAuthenticated = action.payload
        },
        setNameAndEmailId: (state, action) => {
            const accounts = action.payload;
            if (accounts && accounts.length > 0) {
                const account = accounts[0]
                state.emailId = account.username ? account.username : APPROVER_ID
                state.name = account.name ? account.name : 'Admin'
            }
        },
    },
});

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.counter.value)`
export const { updateAuthentication, setToken, setNameAndEmailId } = authSlice.actions;




export const loginDispatch =
    ({ emailId, password }) =>
        async (dispatch) => {
            try {
                const emailIdTrimmed = emailId.trim();
                const passwordTrimmed = password.trim();
                const isAuthenticated = userList.some(
                    (data) =>
                        data.emailId === emailIdTrimmed && data.password === passwordTrimmed
                );
                dispatch(updateAuthentication(isAuthenticated));
                if (isAuthenticated) {
                    Promise.resolve();
                } else {
                    Promise.reject();
                }
            } catch (err) {
                Promise.reject();
                dispatch(updateAuthentication(false));
            }
        };

export const userSelector = ((state) => ({ emailId: state.auth.emailId, name: state.auth.name }));


export default authSlice.reducer;
