import { API_METHODS, API_ROUTES } from 'src/static/api';
import createToken from './create-token';
import { getBearerAuthorizationHeader, getHeaders } from './utils';

const getBody = ({ bookingId, emailId, comments }) => {
    return {
        bookingId,
        emailId,
        comments
    };
};

const updateComments = async ({ token, requestBody }) => {
    try {
        const body = getBody(requestBody);
        const partialHeaders = getHeaders();
        const authorizationHeader = getBearerAuthorizationHeader(token);
        const headers = { ...partialHeaders, ...authorizationHeader };
        console.log({ headers, body }, ' updateComments ');
        const response = await fetch(
            `${API_ROUTES.updateComments}`,
            {
                method: API_METHODS.post,
                headers,
                body: JSON.stringify(body),
            }
        );
        if (response.status === 401) {
            await createToken();
            const access_token = localStorage.getItem('access_token');
            await updateComments({
                token: access_token,
                requestBody,
            });
        } else if (response.status === 200) {
            const responseJson = await response.json();
            if (responseJson.status === 'ACCEPTED') {
                return;
            } else {
                throw new Error(`${response.message}`);
            }
        } else {
            throw new Error(`${response.status} : ${response.statusText}`);
        }
    } catch (err) {
        throw new Error(err.message);
    }
};

export default updateComments;
