import { useEffect } from 'react';
import {
  Route, BrowserRouter, Routes,
} from 'react-router-dom';
import { AuthenticatedTemplate, UnauthenticatedTemplate, useIsAuthenticated, useMsal, useMsalAuthentication } from "@azure/msal-react";

import NoInternet from 'src/components/no-internet-dialog';
import useNetwork from 'src/hooks/useNetwork';
import Home from 'src/pages/home';
import NoMatch from 'src/pages/no-match';
import SignIn from 'src/pages/signin';
import { RequireAuth } from 'src/components/routes';
import { ROUTES } from './static/routes';
import userList from './__mocks__/user-list';
import { InteractionStatus, InteractionType } from '@azure/msal-browser';
import { useDispatch } from 'react-redux';
import { setNameAndEmailId } from './redux/auth';

function App() {
  const isOffline = useNetwork();
  const dispatch = useDispatch()
  const { accounts } = useMsal();

  useMsalAuthentication(InteractionType.Redirect);

  useEffect(() => {
    dispatch(setNameAndEmailId(accounts))
  }, [accounts]);


  return (
    <AuthenticatedTemplate>
      <BrowserRouter>
        <div>
          <Routes>
            <Route exact path={ROUTES.dashboard} element={
              <Home />
            } />
            {/* <Route exact path={ROUTES.signIn} element={
              <UnauthenticatedTemplate>
                <SignIn />
              </UnauthenticatedTemplate>
            }>
            </Route> */}
            <Route path="*" element={<NoMatch />} />
          </Routes>
        </div>
        <NoInternet isOffline={isOffline} />
      </BrowserRouter >
    </AuthenticatedTemplate >
  );
}


export default App 
