export const fetchUserDataDispatch = () => (dispatch) => {
    try {
        dispatch(updateLoadingStatus(API_STATUS.loading));
        const currentDate = formatDDMMMYYYY(new Date());
        getUserData({ currentDate }).then(response => {
            console.log(response, " response ")
            const data = getNormalizedUserData(response)
            dispatch(updateMobileKeyStatus(data));
            dispatch(updateLoadingStatus(API_STATUS.done));
        });
    } catch (err) {
        dispatch(updateLoadingStatus(API_STATUS.failed));
        dispatch(updateErrorMessage(err.message));
    }
};

export const updateStatusDispatch = (data) => (dispatch) => {
    try {
        dispatch(updateStatus(data))
        const currentDate = formatDDMMMYYYY(new Date());
        getUserData({ currentDate }).then(response => {
            console.log(response, " response ")
            const mobileKeyStatus = getNormalizedUserData(response)
            dispatch(updateMobileKeyStatus(mobileKeyStatus));
        })
    } catch (err) {
        dispatch(updateLoadingStatus(API_STATUS.failed));
        dispatch(updateErrorMessage(err.message));
    }
}


export default mobileKeyStatusSlice.reducer;
