import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/query/react";
import { getBasicWithDefaultHeaders, getBearWithDefaultHeaders } from "src/services/utils";
import { API_ROUTES_RTK, API_URL } from "src/static/api";

export const apiSlice = createApi({
  reducerPath: "api",
  baseQuery: fetchBaseQuery({
    baseUrl: API_URL,
  }),
  endpoints: (builder) => ({
    accessToken: builder.mutation({
      query: () => ({
        url: API_ROUTES_RTK.token,
        method: "POST",
        headers: getBasicWithDefaultHeaders(),
        params: {
          client_id: process.env.REACT_APP_CLIENT_ID,
          grant_type: process.env.REACT_APP_GRANT_TYPE,
        },
      }),
    }),
    userData: builder.mutation({
      query: ({ token, requestBody }) => ({
        url: API_ROUTES_RTK.retrieveUserData,
        method: "POST",
        headers: getBearWithDefaultHeaders(token),
        body: requestBody,
      }),
    }),
    sendEmail: builder.mutation({
      query: ({ token, requestBody }) => ({
        url: API_ROUTES_RTK.sendEmail,
        method: "POST",
        headers: getBearWithDefaultHeaders(token),
        body: requestBody,
      }),
    }),
  }),
});

export const {
  useAccessTokenMutation,
  useUserDataMutation,
  useSendEmailMutation,
} = apiSlice;
