import { LogLevel } from '@azure/msal-browser';

console.log(process.env.REACT_APP_AZURE_AD_CLIENT_ID, " process.env.REACT_APP_AZURE_AD_CLIENT_ID ")
console.log(process.env.REACT_APP_AZURE_REDIRECT_URI, " process.env.REACT_APP_AZURE_REDIRECT_URI ")

console.log(process.env.REACT_APP_AZURE_AD_TENANT_ID, " process.env.REACT_APP_AZURE_AD_TENANT_ID ")

export const msalConfig = {
	auth: {
		clientId: process.env.REACT_APP_AZURE_AD_CLIENT_ID,
		authority: `https://login.microsoftonline.com/${process.env.REACT_APP_AZURE_AD_TENANT_ID}`,
		redirectUri: '/',
	},
	cache: {
		cacheLocation: 'sessionStorage',
		storeAuthStateInCookie: false,
	},
	system: {
		loggerOptions: {
			loggerCallback: (level, message, containsPii) => {
				if (containsPii) {
					return;
				}
				switch (level) {
					case LogLevel.Error:
						console.error(message);
						return;
					case LogLevel.Info:
						console.info(message);
						return;
					case LogLevel.Verbose:
						console.debug(message);
						return;
					case LogLevel.Warning:
						console.warn(message);
						return;
				}
			},
		},
	},
};
